@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
body {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 20px;
  margin: 0;
}
.app {
  margin: 0;
  font-size: 20px;
}
.app .room {
  position: fixed;
  top : 0;
  left : 0;
  height : 100%;
  width : 100%;
}
.app .room .bar {
  padding: 10px;
  box-shadow: 0px 0px 10px #cccccc;
  background : white;
  
}
.app .room .bar span.back {
  margin: 0px 10px;
}
.app .room .bar span.name {
  font-weight : bold;
}
.app .room .main {
  padding : 40px 10px 100px 10px;
  overflow: scroll;
  overflow-x:hidden;
  overflow-y:auto;
}
.app .room .main .lm {
  width : 100%;
  overflow: hidden;
  margin-bottom : 8px;
}
.app .room .main .lm:last-child {
  margin-bottom : 50px;
}
.app .room .main .lm .left {
  background : #eeeeee;
  word-wrap: break-word;
  display: inline-block;
  padding: 5px 10px;
  border-radius : 10px;
  float: left;
  max-width: 70%;
}
.app .room .main .lm .center {
  text-align: center;
  font-weight: bold;
  margin: 5px 0px;
}
.app .room .main .lm .right {
  background : #0066CC;
  display: inline-block;
  padding: 5px 10px;
  border-radius : 10px;
  float: right;
  color : white;
  max-width : 70%;
  word-wrap: break-word;
}
.app .room .form {
  border-top : 1px solid silver;
  padding: 10px 15px;
  position: fixed;
  bottom : 0;
  left : 0;
  width : 100%;
  background : white;
  text-align: center;
  
}
.app .room .form .fl {
  display: flex;
}
.app .room .form .cannel {
  background : none;
  border : none;
  border : 1px solid black;
  outline: none;
  border-radius : 10px;
  padding: 10px 15px;
  font-weight: bold;
}
.app .room .form input {
  width : 85%;
  padding : 10px;
  border-radius : 5px;
  border : none;
  outline: none;
  border : 1px solid silver;
}
.app .room .form .like {
  color : blue;
  font-size: 25px;
  margin: 5px 10px 5px 0px;
  width : 15%;
}
.app .home .header {
  display: flex;
  padding: 10px;
  color : white;
  background : #333399;
}
.app .home .header div {
  margin: 0px 20px 0px 5px;
}
a {
  text-decoration: none;
  color : blue;
}
.log .form {
  margin: 20px;
  border : 1px solid black;
  border-radius : 10px;
}
.log .form .title {
  background : #cccccc;
  border-radius : 10px 10px 0px 0px;
  font-weight: bold;
  padding: 10px;
}
.log .form input[type=text] {
  background : none;
  border : none;
  outline : none;
  padding: 10px;
  font-weight: bold;
  width : 100%;
  margin: 10px 0px;
}
.log .form input[type=button] {
  background : #cccccc;
  border : none;
  border-radius : 0px 0px 10px 10px;
  font-weight: bold;
  padding: 10px;
  width : 100%;
}
.spinner {
  width: 40px;
  text-align: center;
}

.spinner > div {
  width: 12px;
  height: 12px;
  background-color: #333;
  border-radius: 100%;
  display: inline-block;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  animation-delay: -0.16s;
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    transform: scale(0);
  } 40% {
    transform: scale(1.0);
  }
}
